.myblog-content .article {
  width: 100%;
}
.myblog-content .article h1 {
  font-family: Chinese Quote, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 36px;
  line-height: 1.389;
  font-weight: 700;
  color: #262626;
  display: inline;
}
.myblog-content .article h2 {
  font-size: 28px;
  line-height: 36px;
  margin: 26px 0 10px 0;
  white-space: normal;
  word-spacing: 1px;
  color: #262626;
  font-weight: bold;
  padding: 0;
}
.myblog-content .article h3 {
  font-size: 24px;
  line-height: 32px;
  margin: 21px 0 5px 0;
  white-space: normal;
  word-spacing: 1px;
  color: #262626;
  font-weight: bold;
  padding: 0;
}
.myblog-content .article .text-right {
  text-align: right;
}
.myblog-content .article .indent-2 {
  text-indent: 2em;
}
.myblog-content .article em {
  color: #8c8c8c;
}
.myblog-content .article .article-title {
  margin-bottom: 32px;
  text-align: center;
}
.myblog-content .article .article-content {
  line-height: 1.74;
  font-size: 15px;
  color: #404040;
  letter-spacing: 0.008em;
}
.myblog-content .article .article-content p {
  white-space: normal;
  margin: 0 0 5px 0;
}
.myblog-content .article .article-content img {
  border: none;
  box-shadow: none;
  visibility: visible;
  margin: 10px 0;
  width: auto;
  max-width: 100%;
}
.myblog-content .article .article-content ul {
  list-style-type: disc;
  margin: 0 0 0 3px;
  padding: 0;
}
.myblog-content .article .article-content ul li {
  white-space: normal;
  margin-left: 23px;
}
.myblog-content .article .article-content a {
  word-wrap: break-word;
  touch-action: manipulation;
}
